import React, { Component } from 'react'
 // class level where no need to pass props and have to mention this.props compulsary
export default class TodoItems extends Component{
   
    render(){
        const changeStyle = {
            color:"#cdcdcd",
            fontStyle:"italic",
            textDecoration:"line-through"
        }
    return(
        <div className="todo-item">
            <input type="checkbox" checked={this.props.item.completed}
            onChange={() => this.props.handleChanged(this.props
            .item.id)} />
            <p style={this.props.item.completed ? changeStyle : null}>{this.props.item.text}</p>
        </div>
    )
    }
}

