import React from 'react';
import TodoItems from './TodoItems'
import DOListData from './DoListData'

export default class App extends React.Component {
    constructor()
        {
        super()
        this.state = {
          todos : DOListData
        }
        this.handleChanged = this.handleChanged.bind(this)
    }

    handleChanged(id){
       this.setState(prevState => {
        const updatedTodos = prevState.todos.map(todo => {
          if(todo.id === id){
            todo.completed = ! todo.completed
          }
          return todo
        })
        return{
          todos :updatedTodos
        }
        })
    }

    render()
    {
    // use dolistdata file to import
    const todata =  this.state.todos.map(data => <TodoItems item={data} handleChanged = {this.handleChanged}/>)

    return (
        <>
          
          <h1 className="colr center"> TODO App </h1>

          <div className="center todo-list">
              {todata}
          </div>
        </>
        );
    }
}

