import React from 'react'
export default function Greeting(){

    // inline css style
    const greet = {
        color : "#0a3973"
    }

    const date = new Date();
    const hours = date.getHours();
    let dayTime;

    (hours<12)? dayTime="Morning":(hours>=12 && hours < 17)? dayTime = "Afternoon" :(hours>=17 && hours < 19) ? dayTime = "Evening":dayTime = "Night" 

    return( <h2 style = {greet}> Good {`${dayTime}`}</h2>)
}


